export const slugify = function (str: string) {
  // slugify   URL      .
  str = str.replace(/^\s+|\s+$/g, ""); // trim
  str = str.toLowerCase();

  // remove accents, swap  for n, etc
  var from = "/_,:;";
  var to = "aaaaaeeeeeiiiiooooouuuunc------";
  for (var i = 0, l = from.length; i < l; i++) {
    str = str.replace(new RegExp(from.charAt(i), "g"), to.charAt(i));
  }

  str = str
    .replace(/[^a-z0-9 -]/g, "") // remove invalid chars
    .replace(/\s+/g, "-") // collapse whitespace and replace by -
    .replace(/-+/g, "-"); // collapse dashes

  return str;
};

export function makeId(length: number) {
  // makeId     .
  var result = "";
  var characters = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
  var charactersLength = characters.length;
  for (var i = 0; i < length; i++) {
    result += characters.charAt(Math.floor(Math.random() * charactersLength));
  }
  return result;
}

/*
 * @name arraysHaveSameContent
 * @description       .
 * @param {any[]} array1
 * @param {any[]} array2
 * @returns {boolean}
 * */
export function arraysHaveSameContent(array1: any[], array2: any[]) {
  const sortedArray1 = array1.slice().sort();
  const sortedArray2 = array2.slice().sort();

  return JSON.stringify(sortedArray1) === JSON.stringify(sortedArray2);
}



/*
* @name saveTokenToLocalStorage
* @description    .
* @param {string} token
* */
export function saveTokenToLocalStorage (token : string) {
  const expirationTimeInMillis = 60 * 60 * 1000; //   
  const expirationDate = new Date().getTime() + expirationTimeInMillis; //      

  localStorage.setItem('access-token', token);
  localStorage.setItem('tokenExpiration', expirationDate.toString())
}

/*
* @name removeTokenFromLocalStorage
* @description    .
* */
function removeTokenFromLocalStorage () {
  localStorage.removeItem('access-token');
  localStorage.removeItem('tokenExpiration');
};

/*
* @name checkTokenExpiration
* @description   ,  .
* @returns {void}
* */
export function checkTokenExpiration() {
  // console.log('checkTokenExpiration ')
  const tokenExpiration = localStorage.getItem('tokenExpiration');

  if (tokenExpiration) {
    const currentTime = new Date().getTime();
    const expirationTime = parseInt(tokenExpiration);

    if (currentTime > expirationTime) {
      //   
      removeTokenFromLocalStorage();
    }
  }
};